@use "../../partials/colors" as *;

.app__menuitem {
  width: 100%;
  margin: 1rem 0;

  display: flex;
  flex-direction: column;

  &-head {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
  &-sub {
    width: 100%;
    margin-top: 0.2rem;
  }

  &-name {
    flex: 1; //git it more space
  }

  &-dash {
    width: 90px;
    height: 1px;
    background-color: $color-golden;
    margin: 0 1rem;
  }

  &-price {
    display: flex;
    justify-content: flex-end;
    align-items: flex-end;
  }
}
